import React, { useMemo, useState } from 'react';
import { withRouter, Link } from 'react-router-dom';
import accounting from 'accounting';

import Checkbox from './Checkbox';

import edit from '../img/edit.svg';
import './place.css';


const Basket = ({ match: { params: { areaId, itemId }}, foodAreas, order }) => {
  const [ faster, setFaster ] = useState(localStorage.getItem(`${itemId}_orderTime`) ? false : true);
  const [ time, setTime ] = useState(localStorage.getItem(`${itemId}_orderTime`) || '');
  const [ selfService, setSelfService ] = useState(localStorage.getItem(`${itemId}_orderService`) == "true");
  const area = foodAreas.filter(area => area.id === areaId)[0];
  const item = area.items.filter(item => item.id === itemId)[0];

  const [ price, products ] = useMemo(() => {
    const foodIds = new Set((item.foods || []).map(item => item.id));

    const products = Object.values(order)
      .filter((value) => {
        const { item: { id }} = value;

        return foodIds.has(id);
      });

    const result = products.reduce((result, value) => {
        const { count, item } = value;

        return result + parseInt(item.price) * parseInt(count);
      }, 0);

    return [ accounting.formatNumber(result, 0, ' '), products ];
  }, [ order, item ]);

  const checkTime = () => {
    if (time.length == 5 && /(2[0-3]|[0-1]\d):[0-5]\d/.test(time)) {
      return true;
    }
    else if (time.length == 4 && /(2[0-3]|[0-1]\d):[0-5]\d/.test('0' + time)) {
      return true;
    }
    else {
      return false;
    }
  }

  const saveProps = () => {
    localStorage.setItem(`${itemId}_orderTime`, time)
    localStorage.setItem(`${itemId}_orderService`, selfService)
  }

  return (
    <div className="Place">
      <header className="Place__header">
        <aside className="Place__trz">
          <h1 className="Place__head">
            <Link to="/" className="Place__logo">
              {area.name}
            </Link>
          </h1>
          <Link to="/edit" className="Place__change-tz">
            <img
              alt="change-profile"
              src={edit}
            />
          </Link>
        </aside>
      </header>
      <aside className="Place__restoraunt">
        <img
          className="Place__restoraunt-logo"
          alt="Fastfood logo"
          src={item.image}
        />
        <h2
          className="Place__restoraunt-name"
        >
          {item.name}
        </h2>
        <p className="Place__restoraunt-type">
          {item.description}
        </p>
      </aside>
      <div className="Place__products-wrapper">
        <ul className="Place__products">
          {products.map(({ item, count }) => (
            <li
              className="Place__product"
              key={item.id}
            >
              <img
                className="Place__product-logo"
                alt="Ordered product logo"
                src={item.image}
              />
              <h3
                className="Place__product-name"
              >
                {item.name}
              </h3>
              <p
                className="Place__product-price"
              >
                Цена: {item.price}
              </p>
              <p
                className="Place__product-count"
              >
                x{count}
              </p>
            </li>
          ))}
        </ul>
        <Link
          className="Place__change-product"
          to={`/place/${areaId}/${itemId}`}
        >
          Изменить
        </Link>
      </div>
      <div className="Place__choice">
        <h3>Время:</h3>
        <div className="Place__choice-item">
          <span>Как можно быстрее</span>
          <Checkbox
            checked={faster}
            onToggle={() => {
              if (faster) {
                setFaster(false);
              } else {
                setTime('');
                localStorage.removeItem(`${itemId}_orderTime`)
                setFaster(true);
              }
            }}
          />
        </div>
        <div className="Place__choice-item">
          <span>Назначить</span>
          <input
            maxLength={5}
            value={time}
            onFocus={() => {
              setFaster(false);
            }}
            onChange={event => {
              setFaster(false);
              setTime(event.target.value.replace (/[A-Za-zА-Яа-яЁё]/, ''));
            }}
            onBlur={() => {
              if (time) {
                setFaster(false);
              }
            }}
          />
        </div>
        <div className="Place__choice-item">
          <h3>С собой</h3>
          <Checkbox checked={(selfService)} onToggle={() => setSelfService(!(selfService))} />
        </div>
        <div className="Place__choice-item">
          <h3>На месте</h3>
          <Checkbox checked={(!selfService)} onToggle={() => setSelfService(!(selfService))} />
        </div>
      </div>
      <footer className="Place__footer">
        <Link to={`/order/${area.id}/${item.id}`} onClick={saveProps.bind(this)} className="Place__order" id={(checkTime() || faster) ? "" : "empty"}>
          {(checkTime() || faster)  ? `Оплата ${price}` : "Для оплаты укажите время"}
        </Link>
      </footer>
    </div>
  );
};

export default withRouter(Basket);
